import React, { useState, useEffect } from "react";

interface AnimatedStepperProps {
  steps: Array<{
    title: string;
    content: React.ReactNode;
  }>;
  className?: string;
  onStepChange?: (step: number) => void;
  autoPlay?: boolean;
  autoPlayInterval?: number;
}

const AnimatedStepper: React.FC<AnimatedStepperProps> = ({
  steps,
  className = "",
  onStepChange,
  autoPlay = true,
  autoPlayInterval = 4000
}) => {
  const [currentStep, setCurrentStep] = useState(0);
  const [direction, setDirection] = useState(1);
  const [isPaused, setIsPaused] = useState(false);

  // Auto-play functionality
  useEffect(() => {
    if (!autoPlay || isPaused) return;

    const interval = setInterval(() => {
      setCurrentStep((prev) => {
        const nextStep = (prev + 1) % steps.length;
        setDirection(1);
        onStepChange?.(nextStep);
        return nextStep;
      });
    }, autoPlayInterval);

    return () => clearInterval(interval);
  }, [autoPlay, autoPlayInterval, isPaused, steps.length, onStepChange]);

  const updateStep = (newStep: number) => {
    if (newStep >= 0 && newStep < steps.length) {
      setDirection(newStep > currentStep ? 1 : -1);
      setCurrentStep(newStep);
      onStepChange?.(newStep);
    }
  };

  const nextStep = () => {
    if (currentStep < steps.length - 1) {
      updateStep(currentStep + 1);
    }
  };

  const prevStep = () => {
    if (currentStep > 0) {
      updateStep(currentStep - 1);
    }
  };

  return (
    <>
      <style dangerouslySetInnerHTML={{
        __html: `
          .stepper-container {
            max-width: 28rem;
            margin: 0 auto;
            border-radius: 2rem;
            box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1),
                        0 10px 10px -5px rgba(0, 0, 0, 0.04);
            background: rgba(0, 0, 0, 0.6);
            backdrop-filter: blur(12px);
            border: 1px solid rgba(255, 255, 255, 0.05);
          }

          .step-indicator-row {
            display: flex;
            width: 100%;
            align-items: center;
            padding: 2rem;
          }

          .step-indicator {
            position: relative;
            cursor: pointer;
            outline: none;
            transition: all 0.3s ease;
          }

          .step-indicator:hover {
            transform: scale(1.1);
          }

          .step-indicator-inner {
            display: flex;
            height: 2rem;
            width: 2rem;
            align-items: center;
            justify-content: center;
            border-radius: 9999px;
            font-weight: 600;
            background: linear-gradient(135deg, #8b5cf6, #3b82f6);
            transition: all 0.3s ease;
          }

          .step-indicator.active .step-indicator-inner {
            background: linear-gradient(135deg, #a855f7, #3b82f6);
            box-shadow: 0 0 20px rgba(139, 92, 246, 0.5);
          }

          .step-indicator.completed .step-indicator-inner {
            background: linear-gradient(135deg, #10b981, #3b82f6);
          }

          .active-dot {
            height: 0.75rem;
            width: 0.75rem;
            border-radius: 9999px;
            background-color: #fff;
            animation: pulse 2s infinite;
          }

          .step-number {
            font-size: 0.875rem;
            color: white;
          }

          .step-connector {
            position: relative;
            margin-left: 0.5rem;
            margin-right: 0.5rem;
            height: 0.125rem;
            flex: 1;
            overflow: hidden;
            border-radius: 0.25rem;
            background-color: #374151;
          }

          .step-connector-inner {
            position: absolute;
            left: 0;
            top: 0;
            height: 100%;
            background: linear-gradient(90deg, #8b5cf6, #3b82f6);
            transition: width 0.5s ease;
          }

          .step-content {
            padding: 2rem;
            min-height: 200px;
            display: flex;
            flex-direction: column;
            position: relative;
            overflow: hidden;
          }

          .stepper-nav {
            margin-top: 2.5rem;
            display: flex;
            justify-content: space-between;
            padding: 0 2rem 2rem;
          }

          .stepper-btn {
            transition: all 0.35s ease;
            border-radius: 9999px;
            padding: 0.5rem 1.5rem;
            font-weight: 500;
            cursor: pointer;
            border: none;
            outline: none;
          }

          .stepper-btn-back {
            color: #9ca3af;
            background: transparent;
          }

          .stepper-btn-back:hover:not(:disabled) {
            color: #6b7280;
            background: rgba(255, 255, 255, 0.05);
          }

          .stepper-btn-back:disabled {
            opacity: 0.5;
            cursor: not-allowed;
          }

          .stepper-btn-next {
            background: linear-gradient(135deg, #8b5cf6, #3b82f6);
            color: white;
          }

          .stepper-btn-next:hover:not(:disabled) {
            transform: translateY(-2px);
            box-shadow: 0 10px 20px rgba(139, 92, 246, 0.3);
          }

          .stepper-btn-next:disabled {
            opacity: 0.5;
            cursor: not-allowed;
          }

          .pause-btn {
            position: absolute;
            top: 1rem;
            right: 1rem;
            background: rgba(255, 255, 255, 0.1);
            border: none;
            border-radius: 50%;
            width: 2rem;
            height: 2rem;
            color: #9ca3af;
            cursor: pointer;
            transition: all 0.3s ease;
            display: flex;
            align-items: center;
            justify-content: center;
          }

          .pause-btn:hover {
            background: rgba(255, 255, 255, 0.2);
            color: white;
          }

          @keyframes pulse {
            0%, 100% { opacity: 1; }
            50% { opacity: 0.5; }
          }

          @keyframes slideInRight {
            from {
              opacity: 0;
              transform: translateX(50px);
            }
            to {
              opacity: 1;
              transform: translateX(0);
            }
          }

          @keyframes slideInLeft {
            from {
              opacity: 0;
              transform: translateX(-50px);
            }
            to {
              opacity: 1;
              transform: translateX(0);
            }
          }

          @keyframes slideOutRight {
            from {
              opacity: 1;
              transform: translateX(0);
            }
            to {
              opacity: 0;
              transform: translateX(50px);
            }
          }

          @keyframes slideOutLeft {
            from {
              opacity: 1;
              transform: translateX(0);
            }
            to {
              opacity: 0;
              transform: translateX(-50px);
            }
          }

          .fade-slide-content {
            opacity: 1;
            transform: translateX(0);
            animation: slideInRight 0.6s ease-out;
          }

          .fade-slide-content.slide-left {
            animation: slideInLeft 0.6s ease-out;
          }

          .progress-bar {
            position: absolute;
            bottom: 0;
            left: 0;
            height: 3px;
            background: linear-gradient(90deg, #8b5cf6, #3b82f6);
            border-radius: 0 0 2rem 2rem;
            transition: width 0.1s linear;
          }
        `
      }} />
      
      <div 
        className={`stepper-container ${className}`}
        onMouseEnter={() => setIsPaused(true)}
        onMouseLeave={() => setIsPaused(false)}
      >
        {/* Pause/Play Button */}
        {autoPlay && (
          <button
            className="pause-btn"
            onClick={() => setIsPaused(!isPaused)}
            title={isPaused ? "Resume" : "Pause"}
          >
            {isPaused ? (
              <svg width="12" height="12" viewBox="0 0 24 24" fill="currentColor">
                <path d="M8 5v14l11-7z"/>
              </svg>
            ) : (
              <svg width="12" height="12" viewBox="0 0 24 24" fill="currentColor">
                <path d="M6 19h4V5H6v14zm8-14v14h4V5h-4z"/>
              </svg>
            )}
          </button>
        )}

        {/* Progress Bar */}
        {autoPlay && !isPaused && (
          <div 
            className="progress-bar"
            style={{ 
              width: `${((currentStep + 1) / steps.length) * 100}%`,
              animation: `progressAnimation ${autoPlayInterval}ms linear infinite`
            }}
          />
        )}

        {/* Step Indicators */}
        <div className="step-indicator-row">
          {steps.map((_, index) => {
            const isActive = index === currentStep;
            const isCompleted = index < currentStep;
            const isNotLastStep = index < steps.length - 1;
            
            return (
              <React.Fragment key={index}>
                <div
                  className={`step-indicator ${isActive ? 'active' : ''} ${isCompleted ? 'completed' : ''}`}
                  onClick={() => updateStep(index)}
                >
                  <div className="step-indicator-inner">
                    {isCompleted ? (
                      <svg className="w-4 h-4 text-white" fill="currentColor" viewBox="0 0 20 20">
                        <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                      </svg>
                    ) : isActive ? (
                      <div className="active-dot" />
                    ) : (
                      <span className="step-number">{index + 1}</span>
                    )}
                  </div>
                </div>
                
                {isNotLastStep && (
                  <div className="step-connector">
                    <div 
                      className="step-connector-inner" 
                      style={{ width: isCompleted ? '100%' : '0%' }}
                    />
                  </div>
                )}
              </React.Fragment>
            );
          })}
        </div>

        {/* Step Content */}
        <div className="step-content">
          <div 
            key={currentStep} 
            className={`fade-slide-content ${direction === -1 ? 'slide-left' : ''}`}
          >
            <h3 className="text-xl font-bold mb-4 bg-gradient-to-r from-primary-400 to-secondary-400 bg-clip-text text-transparent">
              {steps[currentStep]?.title}
            </h3>
            <div className="flex-1 text-gray-300">
              {steps[currentStep]?.content}
            </div>
          </div>
        </div>

        {/* Navigation */}
        <div className="stepper-nav">
          <button
            className="stepper-btn stepper-btn-back"
            onClick={prevStep}
            disabled={currentStep === 0}
          >
            Back
          </button>
          
          <button
            className="stepper-btn stepper-btn-next"
            onClick={nextStep}
            disabled={currentStep === steps.length - 1}
          >
            {currentStep === steps.length - 1 ? 'Complete' : 'Continue'}
          </button>
        </div>
      </div>

      <style dangerouslySetInnerHTML={{
        __html: `
          @keyframes progressAnimation {
            0% { width: 0%; }
            100% { width: ${((currentStep + 1) / steps.length) * 100}%; }
          }
        `
      }} />
    </>
  );
};

export default AnimatedStepper;